import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Navigate,
  Routes,
} from "react-router-dom";
import Page from "./component/Page";

function App() {
  return (
    <Router>
      <Routes>
        <Route path="pages/Sundays" element={<Page />} />
        <Route path="*" element={<Navigate to="pages/Sundays" />} />
      </Routes>
    </Router>
  );
}

export default App;
